<template>
  <div>
    <mu-appbar :title="$route.meta.title">
      <mu-icon-button
        icon='arrow_back'
        slot="left"
        @click="$router.push('home/center')"/>
      <mu-icon-button
        icon='home'
        slot="right"
        @click="$router.push('/home')"/>
    </mu-appbar>
    <div class="profile-avatar">
      <h1>头像</h1>
      <div class="profile-avatar__container">
        <div class="profile-avatar__img">
          <img :src="avatar | profileImgMinify" alt="">
        </div>
        <div class="profile-avatar__action-box">
          <p>支持 jpg、png 格式大小 5M 以内的图片</p>
          <input
            v-if="!reset"
            id="file"
            type="file"
            @change="uploadFile"
            accept="image/png, image/jpeg">
          <mu-raised-button
            class="upload"
            @click="upload"
            label="点击上传"
            icon="cloud_upload"
            primary/>
        </div>
      </div>
    </div>
    <ul class="profile-settings">
      <li v-for="(item, index) in items">
        <div class="profile-settings__container">
          <h1>{{item.title}}</h1>
          <div>
            <mu-text-field class="profile-settings__input-box"
              v-model="item.value"
              :hintText="item.placeholder"
              hintTextClass="profile-settings__input-box--default"
              @focus="edit(item)"/>
            <div class="profile-settings__action-box">
              <button
                v-show="!item.actionShow"
                type="button"
                name="modify"
                @click="modify(item, index)">
                <mu-icon value="mode_edit"/>
                <span>修改</span>
              </button>
              <button
                v-show="item.actionShow"
                type="button"
                name="confirm"
                @click="save(item, index)">
                保存
              </button>
              <button
                v-show="item.actionShow"
                type="button"
                class="cancel"
                name="cancel"
                @click="cancel(item, index)">
                取消
              </button>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { getProfile, editProfile } from '@/util/request/api'
import { s20 } from '@/util/index'

export default {
  name: 'profile',
  data: () => ({
    items: [
      {
        title: '昵称',
        value: '',
        key: 'username',
        placeholder: '填写你的昵称',
        actionShow: false
      },
      {
        title: '职位',
        value: '',
        key: 'jobTitle',
        placeholder: '填写你的职位',
        actionShow: false
      },
      {
        title: '公司',
        value: '',
        key: 'company',
        placeholder: '填写你的公司',
        actionShow: false
      },
      {
        title: '个人介绍',
        value: '',
        key: 'self_description',
        placeholder: '填写职业技能、擅长的事情、喜欢的事情等',
        actionShow: false
      },
      {
        title: '个人主页',
        value: '',
        key: 'blogAddress',
        placeholder: '填写你的个人主页',
        actionShow: false
      }
    ],
    itemsFirst: [],
    avatar: '',
    reset: false
  }),
  methods: {
    async getProfile () {
      const res = await getProfile()
      this.items.forEach((item, index) => {
        item.value = res.data[index]
      })
      this.avatar = res.avatar
      this.itemsFirst = res.data
    },
    edit (item) {
      item.actionShow = true
    },
    modify (item, index) {
      item.actionShow = true
      const inputDOM = document.querySelectorAll('.mu-text-field-input')
      inputDOM[index].focus()
    },
    async save (item, index) {
      item.actionShow = false
      if (item.value === this.itemsFirst[index]) return
      const res = await editProfile(item)
      console.log(res)
    },
    cancel (item, index) {
      item.actionShow = false
      item.value = this.itemsFirst[index]
    },
    get20 () {
      const result = s20()
      console.log(result)
    },
    upload () {
      const fileDOM = document.querySelector('#file')
      fileDOM.click()
    },
    uploadFile (e) {
      this.resetDOM()
    },
    resetDOM () {
      this.reset = true
      setTimeout(() => {
        this.reset = false
      }, 300)
    }
  },
  mounted () {
    this.getProfile()
    this.get20()
  }
}
</script>

<style>
@import url("../../assets/css/common.css");
@component-namespace profile {
  @component avatar {
    margin-top: 60px;
    @extend border-db;
    background-color: #fff;
    padding: 20px 10px;
    h1 {
      font-size: 16px;
      margin: 0;
    }
    @descendent container {
      display: flex;
      margin-top: 10px;
    }
    @descendent img {
      width: 72px;
      height: 72px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    @descendent action-box {
      padding-left: 20px;
      input {
        display: none;
      }
      .upload {
        margin-top: 15px;
      }
    }
  }
  @component settings {
    margin-top: 20px;
    background-color: #fff;
    @extend border-db;
    @descendent container{
      padding: 20px 10px 0;
      h1 {
        font-size: 16px;
        margin: 0;
      }
    }
    @descendent input-box {
      width: 73%;
      @modifier default {
        width: 100%;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
    }
    @descendent action-box {
      display: inline-block;
      width: 25%;
      text-align: right;
      button {
        height: 40px;
        vertical-align: middle;
        background: none;
        border: none;
        padding-left: 0;
        padding-right: 0;
        color: #03a9f4;
      }
      .cancel {
        color: gray;
        margin-left: 10px;
      }
      i {
        font-size: 16px;
        transform: translateY(3px);
      }
      span {
        display: inline-block;
      }
    }
  }
}
</style>



