<template>
  <div class="home">
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <mu-paper class="home-nav">
      <mu-bottom-nav
        :value="bottomNav"
        @change="handleChange"
        shift>
        <mu-bottom-nav-item
          value="recommend"
          title="推荐"
          icon="home"/>
        <mu-bottom-nav-item
          value="front-end"
          title="前端"
          icon="important_devices"/>
        <mu-bottom-nav-item
          value="back-end"
          title="后端"
          icon="storage"/>
        <mu-bottom-nav-item
          value="center"
          title="我"
          icon="perm_identity"/>
      </mu-bottom-nav>
    </mu-paper>
    <login v-if="isShow" @hidden="isShow = false"></login>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from 'vuex'
import login from '@/components/login'

export default {
  name: 'home',
  data: () => ({
    bottomNavColor: '',
    isShow: false
  }),
  computed: {
    ...mapState(['bottomNav']),
    ...mapGetters(['isLogin'])
  },
  components: {
    login: login
  },
  methods: {
    ...mapMutations(['HOME_NAV']),
    handleChange (val) {
      if (val === 'center' && !this.isLogin) {
        this.isShow = true
      } else {
        this.HOME_NAV(val)
        this.$router.push(`/home/${val}`)
      }
    }
  },
  mounted () {
    this.$router.push(`/home/${this.bottomNav}`)
  }
}
</script>

<style scoped>
.home {
  .home-nav {
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
  }
}
</style>



