<template>
  <div>
    <mt-loadmore
      :bottom-method="loadBottom"
      :bottom-all-loaded="allLoaded"
      ref="loadmore">
      <div class="index">
        <div class="index-title">
          <mu-icon value="storage" color="blue"/>
          <h4>精选</h4>
        </div>
          <ul class="index-list">
            <transition-group name="slide-fade">
              <li v-for="(item, index) in list" :key="index">
                <h3 style="-webkit-box-orient: vertical;">
                  <mu-badge :content="item.tagsTitleArray[0]" secondary slot="after"/>
                  {{item.title}}
                </h3>
                <div class="index-list-icon">
                  <mu-icon value="favorite" :size="16" color="gray"/>
                  <span>{{item.collectionCount}}</span>
                  <mu-icon value="account_circle" size="16" color="gray"/>
                  <span>{{item.user.username}}</span>
                  <mu-icon value="thumb_up" size="16" color="gray"/>
                  <span>{{item.subscribersCount}}</span>
                </div>
                <img :src="item.screenshot.url" alt="">
              </li>
            </transition-group>
          </ul>
      </div>
    </mt-loadmore>
  </div>
</template>

<script>
import { Indicator, Loadmore } from 'mint-ui'
import { paramsMaps as Maps, getList } from '@/util/request/api'

export default {
  name: 'back-end',
  data: () => ({
    list: [],
    pageNo: 1,
    pageSize: '',
    allLoaded: false
  }),
  components: [Loadmore],
  methods: {
    async firstRender () {
      const res = await getList(Maps['backend'])
      if (res.hasOwnProperty('data')) {
        this.list = res.data
        Indicator.close()
      }
    },
    async loadBottom () {
      if (this.pageNo === this.pageSize) {
        // after all data are fetched, lock this methods
        this.allLoaded = true
        return
      } else {
        Indicator.open('加载中...')
        const res = await getList(Maps['backend'], this.pageNo * 10)
        setTimeout(() => {
          this.list = [...this.list, ...res.data]
          Indicator.close()
          this.pageNo ++
          this.$refs.loadmore.onBottomLoaded()
        }, 500)
      }
    }
  },
  mounted () {
    Indicator.open('加载中...')
    this.firstRender()
  }
}
</script>

<style>
</style>



