<template>
  <div class="center">
    <div class="header">
      <router-link :to="{name: 'profile'}">
        <div class="header-container">
          <img :src="user.avatar_large | profileImgMinify" alt="">
          <span>{{user.username}}</span>
          <mu-icon value="keyboard_arrow_right" color="gray"/>
        </div>
      </router-link>
    </div>
    <ul class="icon-list">
      <li v-for="item in list">
        <router-link :to="{name: item.name}">
          <mu-icon :value="item.icon" :color="item.color"/>
          <div class="list-main">
            <span>{{item.tag}}</span>
            <span>{{item.value}}</span>
          </div>
        </router-link>
      </li>
    </ul>
    <div class="log-out" @click="dialog = true">
      <span>退出登录</span>
    </div>
    <mu-dialog
      :open="dialog"
      title="对话"
      @close="dialog = false">
      是否退出登录？
      <mu-flat-button
        slot="actions"
        @click="dialog = false"
        label="取消"
        primary/>
      <mu-flat-button
        slot="actions"
        @click="logOut"
        label="确定"
        primary/>
    </mu-dialog>
  </div>
</template>

<script>
import { clear } from '@/util/storage'
import { mapGetters } from 'vuex'

export default {
  name: 'center',
  data: () => ({
    dialog: false,
    list: [
      {
        icon: 'favorite',
        color: 'green',
        tag: '我喜欢的',
        value: '',
        name: ''
      },
      {
        icon: 'reorder',
        color: 'blue',
        tag: '收藏集',
        value: '',
        name: ''
      },
      {
        icon: 'remove_red_eye',
        color: 'gray',
        tag: '阅读过的文章',
        value: '',
        name: ''
      },
      {
        icon: 'bookmark_border',
        color: 'gray',
        tag: '标签管理',
        value: '',
        name: 'tags'
      }
    ]
  }),
  computed: {
    ...mapGetters(['user'])
  },
  methods: {
    render () {
      const Map = [
        'collectedEntriesCount',
        'totalCollectionsCount',
        'viewedEntriesCount',
        'subscribedTagsCount'
      ]
      ;((i) => {
        this.list.forEach((item, index) => {
          let unit = index % 2 === 0 ? '篇' : '个'
          item.value = i[Map[index]] + unit
        })
      })(this.user)
    },
    logOut () {
      this.dialog = false
      clear()
      this.$router.push('recommend')
    }
  },
  activated () {
    this.render()
  }
}
</script>

<style>
@import "../../assets/css/common.css";
.header {
  @extend .border-db;
  background-color: #fff;
  padding: 20px;
  margin-top: 20px;
  position: relative;
  .header-container {
    display: flex;
    align-items: center;
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
    span {
      margin-left: 10px;
    }
  }
  i {
    position: absolute;
    right: 20px;
    top: 50%;
    margin-top: -12px;
  }
}
.icon-list {
  @extend .border-db;
  margin-top: 20px;
  background-color: #fff;
  li {
    position: relative;
    padding: 10px 20px 10px 50px;
    .list-main {
      display: flex;
      justify-content: space-between;
      span:first-child {
        color: black;
      }
    }
    i {
      position: absolute;
      top: 8px;
      left: 15px;
    }
  }
  li:not(:last-child) {
    @extend .border-bottom;
  }
}
.log-out {
  @extend .border-db;
  background-color: #fff;
  padding: 10px;
  margin-top: 20px;
  text-align: center;
  color: #ff4081;
}
</style>



