<template>
  <div>
    <mt-loadmore
      :top-method="loadTop"
      :bottom-method="loadBottom"
      :bottom-all-loaded="allLoaded"
      ref="loadmore">
      <div class="index">
        <div class="index-title">
          <mu-icon value="whatshot" color="blue"/>
          <h4>精选推荐</h4>
        </div>
          <ul class="index-list">
            <transition-group name="slide-fade">
              <li v-for="(item, index) in list" :key="index" @click="detail(item.url)">
                <h3 style="-webkit-box-orient: vertical;">
                  {{item.title}}
                </h3>
                <div class="index-list-tag">
                  <i v-if="index < 3" v-for="(tag,index) in item.tagsTitleArray" v-color="tag">{{tag}}</i>
                </div>
                <div class="index-list-icon">
                  <mu-icon value="favorite" color="gray"/>
                  <span>{{item.collectionCount}}</span>
                  <mu-icon value="account_circle" color="gray"/>
                  <span>{{item.user.username}}</span>
                  <mu-icon value="thumb_up" color="gray"/>
                  <span>{{item.subscribersCount}}</span>
                </div>
                <img :src="item.screenshot.url" alt="">
              </li>
            </transition-group>
          </ul>
      </div>
    </mt-loadmore>
  </div>
</template>

<script>
import { Indicator, Loadmore } from 'mint-ui'
import { paramsMaps as Maps, getList } from '@/util/request/api'
import { has } from '@/util/index'

export default {
  name: 'recommend',
  data: () => ({
    list: [],
    pageNo: 1,
    pageSize: '',
    allLoaded: false
  }),
  components: [Loadmore],
  methods: {
    async firstRender () {
      const res = await getList(Maps['index'])
      if (has.call(res, 'data')) {
        this.list = res.data
        Indicator.close()
      }
    },
    async loadBottom () {
      if (this.pageNo === this.pageSize) {
        // after all data are fetched, lock this methods
        this.allLoaded = true
        return
      } else {
        Indicator.open('加载中...')
        const res = await getList(Maps['index'], this.pageNo * 10)
        setTimeout(() => {
          this.list = [...this.list, ...res.data]
          Indicator.close()
          this.pageNo ++
          this.$refs.loadmore.onBottomLoaded()
        }, 500)
      }
    },
    detail (link) {
      window.location.href = link
    },
    loadTop () {
      Indicator.open('加载中...')
      this.$refs.loadmore.onTopLoaded()
      this.firstRender()
    }
  },
  mounted () {
    Indicator.open('加载中...')
    this.firstRender()
  }
}
</script>

<style>
@import "../../assets/css/common.css";
.index {
  margin: 10px 0;
  .index-title {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    padding: 5px 10px 2px;
    @extend .border-db;
    background-color: #fff;
    h4 {
      margin-left: 5px;
      line-height: 2;
      color: rgb(33, 150, 243);
    }
  }
  .index-list {
    li {
      background-color: #fff;
      height: 110px;
      @extend .border-bottom;
      position: relative;
      padding-right: 100px;
      h3 {
        font-size: 16px;
        padding: 10px 12px 0;
        @include text-overflow(2);
        min-height: 58px;
        word-break: break-all;
        div {
          vertical-align: middle;
        }
      }
      .index-list-icon,
      .index-list-tag {
        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-start;
        padding: 0 10px;
        margin-top: 5px;
        i:not(:first-child), span {
          margin-left: 5px;
        }
        i {
          font-size: 16px;
        }
        span {
          font-size: 12px;
          max-width: 100px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
      .index-list-tag {
        flex-flow: row wrap;
        i {
          padding: 4px 4px 2px;
          font-size: 12px;
          font-weight: 600;
          line-height: 1;
          color: #fff;
          border-radius: 2px;
          box-shadow: inset 0 -1px 0 rgba(27,31,35,0.12);
        }
      }
      img {
        position: absolute;
        display: block;
        width: 80px;
        height: 80px;
        top: 15px;
        right: 10px;
        border-radius: 2px;
      }
    }
  }
}
</style>



