<template>
  <div class="login" @click="state = 'normal'">
    <form class="">
      <div class="panda">
        <img v-show="state === 'normal'" src="https://gold-cdn.xitu.io/v3/static/img/normal.0447fe9.png" alt="">
        <img v-show="state === 'greeting'" src="https://gold-cdn.xitu.io/v3/static/img/greeting.1415c1c.png" alt="">
        <img v-show="state === 'blindfold'" src="https://gold-cdn.xitu.io/v3/static/img/blindfold.58ce423.png" alt="">
      </div>
      <mu-icon @click.stop="hidden" value="close" color="gray"/>
      <div class="panel">
        <h1 class="title">登录</h1>
        <div class="input-box">
          <input
            @focus="state = 'greeting'"
            @click.stop="state = 'greeting'"
            v-model.trim="name"
            id="name" type="text" name="" placeholder="邮箱/手机">
        </div>
        <div class="input-box">
          <input
            v-if="inputType === 'password'"
            @focus="state = 'blindfold'"
            @click.stop="state = 'blindfold'"
            @blur="state = 'normal'"
            v-model.trim="password"
            @keyup.enter="loginByAccount"
            type="password"
            id="password" name="" placeholder="密码">
          <input
            v-else
            @focus="state = 'blindfold'"
            @click.stop="state = 'blindfold'"
            @blur="state = 'normal'"
            v-model.trim="password"
            @keyup.enter="loginByAccount"
            type="text"
            id="password" name="" placeholder="密码">
          <mu-icon @click.stop="showPassword" value="remove_red_eye" color="gray"/>
        </div>
        <mu-raised-button @click.stop="loginByAccount" label="登录" class="button" primary/>
      </div>
    </form>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
import { mapActions, mapState } from 'vuex'

export default {
  name: 'login',
  data () {
    return {
      state: 'normal',
      name: '',
      password: '',
      inputType: 'password'
    }
  },
  computed: {
    ...mapState([
      'bottomNav'
    ])
  },
  props: ['isShow'],
  methods: {
    ...mapActions(['login']),
    hidden () {
      this.$emit('hidden')
    },
    showPassword () {
      [this.inputType, this.state] =
        this.inputType === 'password'
          ? ['text', 'greeting']
          : ['password', 'blindfold']
    },
    async loginByAccount () {
      if (!this.name) {
        document.querySelector('#name').focus()
        return Toast('邮箱或手机号不能为空')
      }
      if (!this.password) {
        document.querySelector('#password').focus()
        return Toast('密码不能为空')
      }

      const res = await this.login({
        mobilePhoneNumber: this.name,
        password: this.password
      })
      console.log(res)
      if (res.code === 200) {
        Toast('登录成功')
        this.$router.push(`/home/${this.bottomNav}`)
        this.hidden()
      }
    }
  }
}
</script>

<style>
.login {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,.3);
  z-index: 500;
  form {
    position: relative;
    padding: 24px;
    width: 310px;
    background-color: #fff;
    border-radius: 2px;
    .panda > img {
      position: absolute;
      top: 0;
      left: 50%;
      width: 120px;
      transform: translate(-50%,-75.8%);
      z-index: 1;
      &:first-child {
        transform: translate(-50%,-83%);
      }
      &:last-child {
        width: 102px;
        transform: translate(-50%,-75%);
      }
    }

    & > i {
      float: right;
      cursor: pointer;
      opacity: .4;
    }
    .panel {
      h1 {
        margin: 0;
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 20px;
      }
      .input-box {
        position: relative;
        margin-bottom: 10px;
        input {
          width: 100%;
          height: 40px;
          padding: 10px;
          border: 1px solid #e9e9e9;
          border-radius: 2px;
          outline: none;
          &:focus {
            border-color: #007fff;
          }
        }
        i {
          position: absolute;
          right: 5px;
          top: 8px;
        }
      }
      button {
        width: 100%;

      }
    }
  }
}
</style>



