<template>
  <div class="tags">
    <mu-appbar :title="$route.meta.title">
      <mu-icon-button
        icon='arrow_back'
        slot="left"
        @click="$router.push('home/center')"/>
      <mu-icon-button
        icon='home'
        slot="right"
        @click="$router.push('/home')"/>
    </mu-appbar>
    <ul class="tag-list"
      v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10"
      infinite-scroll-immediate-check="check">
      <li v-for="tag in myTags">
        <div class="info">
          <div class="img">
            <img :src="(bottomNav === 'favorite'
              ? (tag.tag && tag.tag.icon.url)
              : (tag.icon && tag.icon.url))
                || img " alt="">
          </div>
          <h3>{{bottomNav === 'favorite'
            ? (tag.tag &&tag.tag.title)
            : (tag.title && tag.title)}}
          </h3>
          <div class="meta">
            <span>{{bottomNav === 'favorite'
              ? (tag.tag && tag.tag.subscribersCount)
              : (tag.subscribersCount && tag.subscribersCount)}}
              关注
            </span>
            <span>{{bottomNav === 'favorite'
              ? (tag.tag && tag.tag.entriesCount)
              : (tag.entriesCount && tag.entriesCount)}}
              文章
            </span>
          </div>
        </div>
        <div class="btn">
          <mu-raised-button
            v-if="tag.btnText === '已关注'"
            :label="tag.btnText"
            @click="cancel(tag)"
            secondary/>
          <mu-raised-button
            v-else
            :label="tag.btnText"
            @click="subscribe(tag)"
            default/>
        </div>
      </li>
    </ul>
    <mu-bottom-nav
      class="nav"
      :value="bottomNav"
      @change="handleChange">
      <mu-bottom-nav-item
        value="favorite"
        title="已关注标签"
        icon="favorite"/>
      <mu-bottom-nav-item
        value="bookmark_border"
        title="全部标签"
        icon="bookmark_border"/>
    </mu-bottom-nav>
  </div>
</template>

<script>
import {
  getSelfTags,
  getDeleteTagObjectId,
  getAllTagsDetail
} from '@/api/tags'
import { has } from '@/util/index'
import { mapActions } from 'vuex'

export default {
  name: 'tags',
  data: () => ({
    myTags: [],
    bottomNav: 'favorite',
    allListNo: 1,
    loding: false,
    check: true,
    img: require('@/assets/img-default.png')
  }),
  methods: {
    ...mapActions(['addTag', 'deleteTag']),
    async getSelfTags () {
      const res = await getSelfTags()
      if (has.call(res, 'data')) this.myTags = res.data
    },
    async getAllTagsDetail () {
      const res = await getAllTagsDetail()
      if (has.call(res, 'data')) this.myTags = res.data
    },
    async loadMore () {
      this.loading = true
      if (this.bottomNav === 'favorite') return

      const res = await getAllTagsDetail(this.allListNo * 40)
      if (has.call(res, 'data')) {
        // add new list
        this.myTags = [...this.myTags, ...res.data]
        this.allListNo ++
        this.loading = false
      }
    },
    getObjectId (item) {
      return this.bottomNav === 'favorite'
        ? item.tag.objectId
        : item.objectId
    },
    async cancel (tag) {
      const objectId = this.getObjectId(tag)
      const objectIdRes = await getDeleteTagObjectId(objectId)
      if (has.call(objectIdRes, 'data')) {
        const res = await this.deleteTag(objectIdRes.data)
        tag.btnText = res && '关注'
      }
    },
    async subscribe (tag) {
      const objectId = this.getObjectId(tag)
      const res = await this.addTag(objectId)

      tag.btnText = res && '已关注'
    },
    handleChange (val) {
      this.bottomNav = val
      this.bottomNav === 'favorite'
        ? this.getSelfTags()
        : this.getAllTagsDetail()
    }
  },
  mounted () {
    this.getSelfTags()
  }
}
</script>

<style scoped>
@import "../../assets/css/common.css";
.tags {
  padding: 50px 0;
}
.tag-list {
  li {
    width: 46%;
    padding: 20px 0;
    @extend border;
    display: inline-block;
    margin: 10px 2%;
    background-color: #fff;
    .info {
      .img {
        height: 30px;
        text-align: center;
        img {
          height: 30px;
        }
      }
      h3 {
        text-align: center;
        padding: 5px 0;
        font-size: 18px;
        min-height: 37px;
      }
      .meta {
        text-align: center;
        min-height: 21px;
        span {
          display: inline-block;
          color: gray;
          font-size: 12px;
        }
      }
    }
    .btn {
      text-align: center;
      margin: 10px 0;
    }
  }
}
.nav {
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
}
</style>



