import { Get, Post, Delete, Put } from './fetch'
import { get as StorageGet } from '../storage'

const info = JSON.parse(StorageGet('USER')).user

/**
 * request for entry list
 */

const paramsMaps = {
  index: {
    where: {
      '$or': [{
        'tags': {
          '$in': [{
            '__type': 'Pointer',
            'className': 'Tag',
            'objectId': '5597a05ae4b08a686ce56f6f'
          },
          {
            '__type': 'Pointer',
            'className': 'Tag',
            'objectId': '555eadc1e4b00c57d9962402'
          }
          ]
        }
      },
      {
        'hot': true
      }
      ]
    }
  },
  frontend: {
    where: {
      category: 'frontend',
      createdAt: {
        gte: {
          __type: 'Date',
          iso: '2017-02-26T04:05:55.798Z'
        }
      }
    }
  },
  backend: {
    where: {
      category: 'backend',
      createdAt: {
        gte: {
          __type: 'Date',
          iso: '2017-02-26T05:29:00.022Z'
        }
      }
    }
  }
}

const getList = async function (category, skip = '') {
  const res = await Get('api/classes/Entry?&',
    Object.assign(category,
      {
        skip: skip,
        limit: '10',
        include: 'user',
        order: '-hotIndex'
      }
    ))
  if (res.code === 200) {
    let data = res.data.results
    data.forEach((item) => {
      if (!item.hasOwnProperty('screenshot')) {
        item.screenshot = {
          url: 'https://gold-cdn.xitu.io/images/logo-header.svg'
        }
      }
    })
    return {data: data}
  } else {
    return {code: res.code}
  }
}

/**
 * request for tags
 */

const userData = {
  __type: 'Pointer',
  className: '_User',
  objectId: info.objectId
}

const getSelfTags = async function () {
  const res = await Get('api/classes/Subscribe?&', {
    where: {
      user: userData
    },
    include: 'tag',
    limit: '40',
    order: '-createdAt'
  })
  if (res.code === 200) {
    let data = res.data.results
    data.forEach((item) => {
      item.btnText = '已关注'
    })
    return {data: data}
  } else {
    return {code: res.code}
  }
}

const getAllTags = async function (skip) {
  const res = await Get('api/classes/Tag?&', {
    where: {},
    limit: '40',
    skip: skip,
    order: '-subscribersCount'
  })
  if (res.code === 200) {
    return {data: res.data.results}
  } else {
    return {code: res.code}
  }
}

const getAllTagsDetail = async function (skip = '') {
  const tags = await getAllTags(skip)
  if (tags.hasOwnProperty('data')) {
    let arr = []
    tags.data.forEach(item => {
      arr.push({
        __type: 'Pointer',
        className: 'Tag',
        objectId: item.objectId
      })
    })
    const res = await Get('api/classes/Subscribe?&', {
      where: {
        tag: {
          '$in': arr
        },
        user: userData
      }
    })
    if (res.code === 200) {
      let subscribes = res.data.results
      tags.data.forEach(all => {
        if (subscribes.length > 0) {
          for (let subscribe of subscribes) {
            if (all.objectId === subscribe.tag.objectId) {
              all.btnText = '已关注'
              break
            } else {
              all.btnText = '关注'
            }
          }
        } else {
          all.btnText = '关注'
        }
      })
      return {data: tags.data}
    } else {
      return {code: res.code}
    }
  }
}

const getDeleteTagObjectId = async function (objectId) {
  const res = await Get('api/classes/Subscribe?&', {
    where: {
      user: userData,
      tag: {
        __type: 'Pointer',
        className: 'Tag',
        objectId: objectId
      }
    }
  })
  if (res.code === 200) {
    let data = res.data.results[0].objectId
    return {data: data}
  } else {
    return {code: res.code}
  }
}

const deleteTag = async function (objectId) {
  const res = await Delete(`api/classes/Subscribe/${objectId}`)
  return res.code === 200 && true
}

const addTag = async function (objectId) {
  const res = await Post('api/classes/Subscribe?', {
    tag: {
      __type: 'Pointer',
      className: 'Tag',
      objectId: objectId
    },
    user: userData
  })
  return res.code === 201 && true
}

/**
 * request for profile
 */

const getProfile = async function () {
  const res = await Get('api/classes/_User?&', {
    where: {
      objectId: info.objectId
    },
    limit: 1
  })
  if (res.code === 200) {
    let data = res.data.results[0]
    let info = [
      data.username || '',
      data.jobTitle || '',
      data.company || '',
      data.self_description || '',
      data.blogAddress || ''
    ]
    return {data: info, avatar: data.avatar_large}
  } else {
    return {code: res.code}
  }
}

const editProfile = async function ({key, value}) {
  let data = {}
  data[key] = value
  const res = await Put(`api/classes/_User/${info.objectId}?`, data)
  return res.code === 200 && true
}

export {
  paramsMaps,
  getList,
  getSelfTags,
  getAllTagsDetail,
  deleteTag,
  addTag,
  getDeleteTagObjectId,
  getProfile,
  editProfile
}



